.text{font-size:var(--font-size);font-weight:var(--font-weight);letter-spacing:var(--font-spacing);line-height:var(--font-height)}.text:has(span){display:flex;align-items:center;gap:.5em}.text:has(span) span{display:flex;align-items:center;height:.25em;width:.25em;background-color:var(--foreground-color);border-radius:50%}.text.-subheadline{font-size:var(--subheadline-size);font-weight:var(--subheadline-weight);letter-spacing:var(--subheadline-spacing);line-height:var(--subheadline-height)}.text.-label{font-size:var(--label-size);font-weight:var(--label-weight);letter-spacing:var(--label-spacing);line-height:var(--label-height)}.text.-subtext{font-size:var(--subtext-size);font-weight:var(--subtext-weight);letter-spacing:var(--subtext-spacing);line-height:var(--subtext-height)}.text.-dm{opacity:.5}a.text::before{content:"";position:absolute;display:block;left:0;bottom:0;width:100%;height:1px;background:currentColor}a.text:hover::before{opacity:0}